.slots {
  margin: 2rem 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  overflow: visible;

  &--details {
    margin: 1rem 0;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
  }

  &--homepage {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
  }

  .slot {
    position: relative;
    display: inline-block;

    &--homepage {
      height: 275px;
      width: 275px;
    }

    &--details {
      height: 120px;
      width: 125px;
    }
  }

  section {
    position: absolute;
    border-radius: 15px !important;
    overflow: hidden;
    // background-color: #ffffffc2;
    border-radius: 2px;
    // border: 1px solid lightgrey;
    color: white;
    font-family: sans-serif;
    text-align: center;
    font-size: 150px;
    // line-height: 340px;
    cursor: default;
  }

  .section--homepage {
    width: 260px;
    height: 260px;
    width: 260px;
    height: 260px;
    line-height: 340px;
  }

  .section--details {
    height: 110px;
    width: 110px;
    line-height: 90px;
  }

  .container {
    position: absolute;
    top: 2px;
    width: 100%;
    transition: top ease-in-out 1.5s;
    text-align: center;

    div {
      span {
        svg {
          path {
            fill: #ed3501;
            // stroke: #ed3501;
          }
        }
      }

      .icon-container {
        &--homepage {
          svg {
            height: 200px;
            width: 200px;
          }
        }

        &--details {
          svg {
            height: 80px;
            width: 80px;
          }
        }
      }
    }
  }

  .roll {
    width: 215px;
    cursor: pointer;
    background-color: yellow;
    padding: 10px;
    text-align: center;
    font-size: 20px;
    border-radius: 20px;
    border: 3px solid black;
    margin: 0 auto;
  }

  .rolling {
    animation: blinkingText 1.2s infinite;
  }

  .slots-cta {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }

  @keyframes blinkingText {
    0% {
      color: #000;
    }
    49% {
      color: #000;
    }
    60% {
      color: transparent;
    }
    99% {
      color: transparent;
    }
    100% {
      color: #000;
    }
  }
}
